import React, { useEffect, useState } from 'react'
import { Button } from '@material-ui/core'
import { useForm, SubmitHandler, Control, FieldValues } from 'react-hook-form'
import { useLocation, useNavigate } from 'react-router-dom'
import { yupResolver } from '@hookform/resolvers/yup'
import useStyles from './OnboardingFormPersonal.styles'
import noAvatar from '../../../../assets/nopic-onboard.png'
import { onboardingPersonalSchema } from '../../../../utils/schemas'
import {
  ControlledAvatarField,
  ControlledCityIdField,
  ControlledDateField,
  ControlledSelectField,
  ControlledTextField,
} from '../../../Sub/FormElements'
import { PersonalInfo } from '../../../../services/types/onboarding/onboarding-types'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setPersonalInfo } from '../../../../services/slices/onboarding-slice'

const OnboardingFormPersonal = () => {
  const classes = useStyles()
  const location = useLocation()
  const navigate = useNavigate()
  const dispatch = useAppDispatch()

  const defaultAvatar = location.state?.avatar
  const { personalInfo } = useAppSelector((store) => store.onboarding)

  const [avatar, setAvatar] = useState<string | null>(noAvatar)
  const [date, setDate] = useState<Date | string | undefined>(personalInfo?.birth_date)
  const defaultValues: PersonalInfo = personalInfo || {
    first_name: '',
    last_name: '',
    city: null,
    birth_date: '',
    gender: '',
  }

  const {
    control,
    handleSubmit,
    register,
    setValue,
    getValues,
    watch,
    formState: { errors },
  } = useForm<PersonalInfo>({
    defaultValues,
    resolver: yupResolver(onboardingPersonalSchema),
    mode: 'onChange',
  })

  const value = getValues('birth_date')
  const genderValue = watch('gender')

  useEffect(() => {
    if (defaultAvatar) {
      const newAva = URL.createObjectURL(defaultAvatar)
      setAvatar(newAva)
      setValue('image', defaultAvatar, { shouldValidate: true, shouldDirty: true })
    } else {
      setAvatar(null)
      setValue('image', null, { shouldValidate: true, shouldDirty: true })
    }
  }, [defaultAvatar])

  useEffect(() => {
    register('gender')
    register('birth_date')
  }, [register])

  useEffect(() => {
    setDate((value as Date) || null)
  }, [setDate, value])

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files) return
    const newAva = URL.createObjectURL(e.target.files[0])
    setAvatar(newAva)
    setValue('image', e.target.files[0], { shouldValidate: true, shouldDirty: true })
  }

  const handleChangeGender = (e: React.ChangeEvent<{ name?: string | undefined; value: string }>) => {
    setValue('gender', e.target.value, { shouldValidate: true, shouldDirty: true })
  }

  const onSubmit: SubmitHandler<PersonalInfo> = (formData) => {
    dispatch(setPersonalInfo(formData))
    navigate('/onboarding/experience', { state: { avatar: formData?.image } })
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)} className={classes.form}>
      <fieldset className={classes.fieldset}>
        <ControlledAvatarField avatar={avatar} helperText={errors?.image?.message} onChange={handleImageChange} />
        <ControlledTextField
          labelTitle="Имя"
          fieldName="first_name"
          placeholder="Имя"
          control={control}
          error={!!errors.first_name?.message}
          helperText={errors.first_name?.message}
          maxWidth={386}
        />
        <ControlledTextField
          labelTitle="Фамилия"
          fieldName="last_name"
          placeholder="Фамилия"
          control={control}
          error={!!errors.last_name?.message}
          helperText={errors.last_name?.message}
          maxWidth={386}
        />
        <ControlledSelectField
          labelTitle="Пол"
          tipContent="Выбери пол"
          value={genderValue}
          error={!!errors?.gender?.message}
          helperText={errors?.gender?.message}
          onChange={handleChangeGender}
          items={[
            { id: 'male', name: 'Мужской' },
            { id: 'female', name: 'Женский' },
          ]}
          maxWidth={386}
          tipBlack={undefined}
          disabled={undefined}
          selectAll={undefined}
          height={undefined}
        />
        <ControlledCityIdField
          control={control}
          error={!!errors?.city?.message}
          helperText={errors?.city?.message}
          width={386}
        />
        <ControlledDateField
          labelTitle="Дата рождения"
          fieldName="birth_date"
          dateValue={date}
          error={!!errors.birth_date?.message}
          helperText={errors.birth_date?.message}
          onChange={(dateValue: string) =>
            setValue('birth_date', dateValue, { shouldValidate: true, shouldDirty: true })
          }
        />
        <Button className={classes.formButton} type="submit">
          Далее
        </Button>
      </fieldset>
    </form>
  )
}

export default OnboardingFormPersonal
